"use client"

import * as React from "react"
import { Loader2 } from "lucide-react"
import { useRouter } from "next/navigation"
import { toast } from "sonner"

import { cn } from "@/lib/utils"
import { useAuth } from "@/features/auth"
import { Button } from "@/components/ui/button"
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"

export function LoginForm({
  className,
  ...props
}) {
  const router = useRouter()
  const { loginWithOpenId, isAuthenticated } = useAuth()
  const [isSubmitting, setIsSubmitting] = React.useState(false)

  const handleSsoLogin = React.useCallback(async () => {
    if (isSubmitting) return
    setIsSubmitting(true)
    try {
      const user = await loginWithOpenId({ provider: "demo-sso" })
      toast.success(`${user?.name ?? "사용자"}님, 환영합니다!`)
      router.push("/")
    } catch (error) {
      console.error(error)
      toast.error("SSO 로그인에 실패했습니다. 잠시 후 다시 시도해주세요.")
    } finally {
      setIsSubmitting(false)
    }
  }, [isSubmitting, loginWithOpenId, router])

  React.useEffect(() => {
    if (isAuthenticated) {
      router.replace("/")
    }
  }, [isAuthenticated, router])

  return (
    <div className={cn("flex flex-col gap-6", className)} {...props}>
      <Card>
        <CardHeader>
          <CardTitle>Single Sign-On</CardTitle>
          <CardDescription>
            조직 계정으로 바로 로그인합니다. 버튼 클릭만으로 인증이 완료됩니다.
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="flex flex-col gap-4">
            <Button
              type="button"
              size="lg"
              onClick={handleSsoLogin}
              disabled={isSubmitting}
              className="font-semibold"
            >
              {isSubmitting ? (
                <>
                  <Loader2 className="mr-2 size-4 animate-spin" aria-hidden="true" />
                  인증 중...
                </>
              ) : (
                "SSO Login"
              )}
            </Button>
            <p className="text-sm text-muted-foreground">
              현재는 데모 환경이므로 항상 로그인에 성공합니다. 폐쇄망으로 이전할 때는
              <code className="mx-1 rounded bg-muted px-1 py-0.5 text-xs">/api/auth/openid</code>
              엔드포인트에서 실제 SSO 서버를 호출하도록 교체하세요.
            </p>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
